import React from 'react'

const MagicButton = ({
    title, icon, position, handleClick, otherClasses
}:{
    title: string; icon: React.ReactNode, position: string; handleClick?: () => void; otherClasses?: string;
}) => {
  return (

    <button className="p-[2px] h-12 w-full relative inline-flex overflow-hidden rounded-lgs focus:outline-none md:w-60 md:mt-10  inset-0 bg-gradient-to-r from-indigo-500 to-violet-500 rounded-lg" onClick={handleClick}>
  <div className={`inline-flex h-full w-full cursor-pointer items-center justify-center rounded-lg bg-slate-950 px-7 text-sm font-medium text-white backdrop-blur-3xl gap-2 ${otherClasses} z-100`}>
    {position === 'left' && icon}
    {title}
    {position === 'right' && icon}
  </div>
</button>
  )
}

export default MagicButton

